<template>
  <div style="margin-top: 50px">
    <el-form :model="value" :rules="rules" ref="productInfoForm" label-width="120px" style="width: 600px" size="small">
      <el-form-item label="菜品分类：" prop="categoryId">
        <el-cascader
          v-model="selectProductCateValue"
          :options="productCateOptions">
        </el-cascader>
      </el-form-item>
      <el-form-item label="菜品名称：" prop="name">
        <el-input v-model="value.name"/>
      </el-form-item>
      <el-form-item label="菜品单价：" prop="price">
        <el-input-number v-model="value.price" :min="0" :precision="2" :step="0.1"/>
      </el-form-item>
      <el-form-item label="菜品描述：" prop="detail">
        <el-input
          :autoSize="true"
          v-model="value.detail"
          type="textarea"
          placeholder="请输入内容"/>
      </el-form-item>
      <el-form-item label="菜品图片：" prop="picture">
        <el-upload
          class="avatar-uploader"
          :action=url
          name="file"
          :on-success="handleUpload"
          :show-file-list="false">
          <img v-if="value.picture" :src="value.picture" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button type="primary" size="medium" @click="commit('productInfoForm')">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {getAllCategoryList} from '@/api/productCate'
  import {createProduct, updateProduct} from '@/api/product';

  export default {
    name: "ProductInfoDetail",
    props: {
      value: Object,
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        url: 'http://localhost:8088/manager/uploadFile/-999',
        hasEditCreated: false,
        host: 'localhost:8088/images',
        //选中商品分类的值
        selectProductCateValue: [],
        productCateOptions: [],
        rules: {
          name: [
            {required: true, message: '请输入菜品名称', trigger: 'blur'},
            {min: 2, max: 140, message: '长度在 2 到 140 个字符', trigger: 'blur'}
          ],
          categoryId: [{required: true, message: '请选择分类', trigger: 'blur'}],
          price: [{required: true, message: '请输入价格', trigger: 'blur'}],
          detail: [{required: true, message: '请选择菜品描述', trigger: 'blur'}],
          picture: [{required: true, message: '请选择图片', trigger: 'blur'}],

        }
      };
    },
    created() {
      this.getProductCateList();

    },

    computed: {
      //商品的编号
      productId() {
        return this.value.id;
      }
    },
    watch: {
      productId: function (newValue) {
        if (!this.isEdit) {
          return;
        }
        if (this.hasEditCreated) return;
        if (newValue === undefined || newValue == null || newValue === 0) return;
        this.handleEditCreated();
        this.url = 'http://localhost:8088/manager/uploadFile/'+this.value.id
      },
      selectProductCateValue: function (newValue) {
        if (newValue != null && newValue.length === 1) {
          this.value.categoryId = newValue[0];
        } else {
          this.value.categoryId = null;
        }
      }
    },
    methods: {
      handleUpload(response, file, fileList) {
        this.value.picture = response.data
      },

      //处理编辑逻辑
      handleEditCreated() {
        if (this.value.categoryId != null) {
          this.selectProductCateValue.push(this.value.categoryId);
        }
        this.hasEditCreated = true;
      },
      getProductCateList() {
        getAllCategoryList(1, 100).then(response => {
          let list = response.data.list;
          this.productCateOptions = [];
          for (let i = 0; i < list.length; i++) {
            this.productCateOptions.push({label: list[i].name, value: list[i].id})
          }
        });
      },
      commit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.isEdit) {
              updateProduct(this.value).then(response => {
                this.$message({
                  type: 'success',
                  message: '提交成功',
                  duration: 1000
                });
                this.$router.back();
              });
            } else {
              createProduct(this.value).then(response => {
                this.$message({
                  type: 'success',
                  message: '提交成功',
                  duration: 1000
                });
                this.$router.push({path: '/pms/product'});
              });
            }
          } else {
            this.$message({
              message: '验证失败',
              type: 'error',
              duration: 1000
            });
            return false;
          }
        });
      }
    }
  }
</script>

<style scoped>

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



